import React from 'react';
import Router from 'preact-router';
import {
    createHashHistory,
} from 'history';


import Home from '../../scenes/Home';
// import Home from '../../scenes/Home';


export default class App extends React.Component {
    componentDidUpdate() {
        window.scrollTop = 0;
    }

    render() {
        return (
            <Router history={createHashHistory()}>
                <Home path="/" />
                <Home path="/test" />
            </Router>
        );
    }
}



