import React from 'react';
import classNames from 'classnames';
import find from 'lodash/find';


import Card from 'App/components/Card';


const ProjectList = ({ projects, projectsConfig, className }) => {
    const projectListClassName = classNames(
        'o-list-unstyled',
        'o-portfolio',
        className,
    );

    return (
        <ul className={projectListClassName}>
            {projectsConfig.map((projectConfig) => {
                const project = find(projects, p => projectConfig.slug === p.slug);

                if (project === undefined) {
                    console.warn(`No project in 'projects' matches ${projectConfig.slug}`);
                    return null;
                }

                const portfolioItemClassNames = classNames(
                    'o-portfolio__item',
                    {
                        'o-portfolio__item--large': projectConfig.large,
                        'o-portfolio__item--bottom': projectConfig.bottom,
                        'o-portfolio__item--gap': projectConfig.gap,
                    },
                );

                return (
                    <li key={project.slug} className={portfolioItemClassNames}>
                        <Card project={project} large={projectConfig.large} />
                    </li>
                );
            })}
        </ul>
    );
};


export default ProjectList;



